#projects{
    border-color: rgb(204, 62, 6);
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content:center ;
    padding: 10px;
    margin:  auto;
    color: aliceblue;
    height: auto;
    min-height: 100vh;
    width: 100vw;
    background-color: rgba(0, 0,0, 0.9);
/* background-image: url('./pj1.jpeg'); */
background-size: cover;
}
#pj{
color: inherit;
    background-color: black;
}
#project{

    flex-wrap: wrap;
    border-radius: 10px;
    display: flex;
    align-items: center;
    justify-content: space-around;
    width: 50vw;
    background-color: aliceblue;
    margin: 0.5rem auto;
    border-width: 2px;
    border-style: solid;
    border-color: blue;

}
#project>div:first-child{
    margin: 1vw 0px 1vw 1vw;
    padding: 0;
    display: flex;
    justify-content: center;
    align-items: center;
}
#project>div:nth-child(2){
    border-radius: 10px;
    flex-wrap: wrap;
    box-sizing: border-box;
   
    display: flex;
    justify-content: space-around;
    align-items: center;
    margin: 1rem 1rem 1rem 1rem;
    width: 28rem;
    height: 5rem;
    /* background-color: black; */
    background-color: black;
    
}

#project>div:first-child>a>img{
    width: 10rem ;
 border-radius: 10px;
 height: 5rem;   
 box-sizing: border-box;
}
#project>div:nth-child(2)>h1:first-child{
    display: inline-block;
    /* background-color: burlywood; */
}
.play{
    width: 5rem;
    padding: 5px;
    border:  2px solid rgb(221, 217, 217) ;
    border-radius: 10px;
  font-weight: 900;
  font-size: 1.5rem;
    text-decoration: none;
    /* color: black; */
    display: flex;
    position: relative;
    justify-content: center;
    /* text-align: center; */
    align-content: center;
}
.projectcode{
    font-weight: 500;
    margin-bottom: 10px;
    color: rgb(103, 168, 28);
    /* background-color: blue; */
}